import React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';
import MailIcon from '@material-ui/icons/Mail';
import MenuIcon from '@material-ui/icons/Menu';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import RoutesDash from './RoutesDash';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import Collapse from '@material-ui/core/Collapse';
import clsx from 'clsx';
// import Collapse from 'material-ui/core/Collapse';
// import ExpandLess from 'material-ui/icons/ExpandLess';
// import ExpandMore from 'material-ui/icons/ExpandMore';
// import { Link } from 'react-router-dom'

const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
  },
  drawer: {
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  appBar: {
    [theme.breakpoints.up('sm')]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
    },
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up('sm')]: {
      display: 'none',
    },
  },
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    padding: '0',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
}));

function ResponsiveDrawer(props) {

  const { container } = props;
  const classes = useStyles();
  const theme = useTheme();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [expanded, setExpanded] = React.useState({});

  React.useEffect(() => {
    let state = {};
    RoutesDash.map((prop, index) => {
      if (prop.children) {
        state[prop.sidebarName] = false;
      }
      setExpanded(state);
    })
  }, [])

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleExpandClick =  (name) => {
    console.log(name)
    let temp = Object.assign({}, expanded);
    temp[name] = !temp[name];
    setExpanded(temp);
  };


  const drawer = (
    <div>
      {/* <div className={classes.toolbar} /> */}
      <h3 className="m-auto py-3 text-center">Welcome</h3>
      <Divider />
      <MenuList>
        {RoutesDash.map((prop, index) => {
          if (!prop.children) {
            return (<div>
              <MenuItem className="py-0"><h3>{prop.sidebarName}</h3></MenuItem></div>)
          }
          return (
            <div>

              <MenuItem className="py-0"><h3>{prop.sidebarName}</h3><IconButton
                className={clsx(classes.expand, {
                  [classes.expandOpen]: expanded[prop.sidebarName],
                })}
                onClick={() => handleExpandClick(prop.sidebarName)}
                aria-expanded={expanded[prop.sidebarName]}
                aria-label="show more" >
                <ExpandMoreIcon />
              </IconButton> </MenuItem>
              <Collapse in={expanded[prop.sidebarName]} timeout="auto" unmountOnExit>
                {prop.children.map(child => (
                  <MenuItem className="py-0"><h3>{child.sidebarName}</h3></MenuItem>

                ))}
              </Collapse>
            </div>
          )
        }
        )}
      </MenuList>

    </div>
  );

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap>
            HotShot Adz
          </Typography>
        </Toolbar>
      </AppBar>
      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden smUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
    </div>
  );
}

ResponsiveDrawer.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  container: PropTypes.any,
};

export default ResponsiveDrawer;
